:global .component-btn {
    display: -ms-flexbox;
    display: flex;
    padding: 8px 16px;
    border-radius: 4px;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
    max-width: 100%;
    text-decoration: none;
    line-height: 1em;
  }
    :global .component-btn.primary {
      border: solid 0.063rem #2c2c2c;
      background-color: #fff;
      color: #2c2c2c;
    }
    :global .component-btn.primary:hover {
        color: #0097ff;
        border-color: #0097ff;
      }
    :global .component-btn.primary:hover .icon {
          fill: #0097ff;
        }
    :global .component-btn.primary:active {
        color: #007eff;
        border-color: #007eff;
      }
    :global .component-btn.primary:active .icon {
          fill: #007eff;
        }
    :global .component-btn.primary.disabled {
        color: rgba(44, 44, 44, 0.5);
        border-color: rgba(44, 44, 44, 0.5);
        cursor: default;
      }
    :global .component-btn.primary.disabled .icon {
          fill: rgba(44, 44, 44, 0.5);
        }
    :global .component-btn.primary .icon {
        fill: inherit;
        width: 1rem;
        height: 1rem;
        margin-right: 0.75rem;
      }
    :global .component-btn.secondary {
      border: solid 0.063rem #41a9ff;
      background-color: #41a9ff;
      color: #fff;
    }
    :global .component-btn.secondary:hover {
        border-color: #0097ff;
        background-color: #0097ff;
      }
    :global .component-btn.secondary:active {
        border-color: #007eff;
        background-color: #007eff;
      }
    :global .component-btn.secondary.disabled {
        border-color: rgba(65, 168, 255, 0.5);
        background-color: rgba(65, 168, 255, 0.5);
      }
    :global .component-btn.secondary .icon {
        fill: #fff;
        width: 1rem;
        height: 1rem;
        margin-right: 0.75rem;
      }
    :global .component-btn.danger {
      border: solid 0.063rem #ff4164;
      background-color: #ff4164;
      color: #fff;
    }
    :global .component-btn.danger:hover {
        border-color: #ee4164;
        background-color: #ee4164;
      }
    :global .component-btn.danger:active {
        border-color: #dd4164;
        background-color: #dd4164;
      }
    :global .component-btn.danger.disabled {
        opacity: 0.5;
      }
    :global .component-btn.danger .icon {
        fill: #fff;
        width: 1rem;
        height: 1rem;
        margin-right: 0.75rem;
      }



