.root {
  width: 100%;
  margin: 0 auto;
  padding: 16px;
}

.message-list {
  height: 400px;
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow-y: scroll;
  margin-bottom: 10px;
}

.input-wrap {
  display: -ms-flexbox;
  display: flex;
}

.input-wrap .input {
    -ms-flex: 1 1;
        flex: 1 1;
    margin-right: 10px;
  }

.input-wrap :global .component-btn {
      width: 120px;
    }

.message-wrap {
  display: -ms-flexbox;
  display: flex;
  padding: 5px;
  margin-bottom: 5px;
  line-height: 14px;
}

.message-wrap .thumb-wrap {
    margin-right: 10px;
  }

.message-wrap .thumb {
    width: 50px;
    height: 50px;
    border-radius: 5px;
  }

.message-wrap .header {
    padding: 2px 0;
    margin-bottom: 5px;
  }

.message-wrap .sender {
    margin-right: 10px;
    font-weight: bold;
    font-size: 12px;
  }

.message-wrap .header {
    display: -ms-flexbox;
    display: flex;
  }

.message-wrap .date-time {
    width: 120px;
    color: #999;
    font-size: 11px;
  }



