.table {
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
  text-align: center;
  margin: 0;
}

.row {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #ddd;
  font-weight: normal;
}

.row:last-child {
    border-bottom: 0;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }

.row:nth-child(even) {
    background-color: #fafafa;
  }

.col {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
  border-left: 1px solid #ddd;
  padding: 5px 10px;
}

.col:first-child {
    border-left: 0;
  }

.thead {
  background-color: #fbfbfb;
  color: #999;
  border-bottom: 1px solid #ddd;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  overflow: hidden;
}

.thead .col {
    line-height: 30px;
  }

.tbody .row {
    border-bottom: 0;
  }

.tbody .col {
  }



